<template>
<div>
  <div class="content">
        <div class="flex-container">
            <div   :style="styles[0]"  @click="clickElement(0)"  class="flex-element one" data-coun='0'></div>
            <div   :style="styles[1]" @click="clickElement(1)" class="flex-element two" data-coun='1'></div>
            <div   :style="styles[2]" @click="clickElement(2)" class="flex-element three" data-coun='2'></div>
            <div   :style="styles[3]" @click="clickElement(3)" class="flex-element for" data-coun='3'></div>
        </div>
        <div class="radio">
            <div class="radio-item">
                <input v-model="difficulty" type="radio" id="contactChoice2" name="contact" value="1000">
                <label for="contactChoice2">Лёгкий режим</label>
            </div>
            <div class="radio-item">
                <input v-model="difficulty" type="radio" id="contactChoice3" name="contact" value="500" checked="checked">
                <label for="contactChoice3">Сложный режим</label>
            </div>
            <div class="radio-item">
                <input v-model="difficulty" type="radio" id="contactChoice4" name="contact" value="250">
                <label for="contactChoice4">Невероятно сложный</label>
            </div>
            <button  @click="buttonClick" type="button">Начало игры</button>
            <p>К-во ваших очков <span id="counter">{{givenLvlv}}</span></p>
        </div>
    </div>
    <h2 class="game-class"></h2>
    </div>
</template>

<script>


export default {
  name: 'App',
  components: {
  },
  data(){
    return{
    difficulty: 1000,
    colors:['#7fffd4', '#0000ff', '#7fff00', '#9932cc'],
    styles:[{backgroundColor: '#7fffd4'},{backgroundColor: '#0000ff'},{backgroundColor: '#7fff00'},{backgroundColor: '#9932cc'}],
    size: 2,
    lvl:0,
    arrId:[],
    indx:0,
    paly: false,
    item:'',
    count: 0,
    flag:true,
    }
  },
  methods:{
    
  async  startGame(){
    if(this.item ==this.arrId[this.indx]){
          this.indx++
    }else{
       this.indx = -1
    }
    if(this.indx < 0){
      this.size = 2 
      this.indx = 0
      this.paly = false
      this.flag = false
      alert('Вы ошиблись')
      this.buttonClick()
    }else if(this.indx == this.arrId.length){
      this.size +=1
      this.indx = 0
      this.lvl += 1
      this.paly = false
      this.buttonClick()
    }

    },

    pushMassive(idendificatir){
      this.arrId.push(idendificatir)
    },

  clickElement(id,){
      this.item = +id
     if(this.paly){
    this.startGame()
    }
      this.playSound()
      this.styles[this.item].backgroundColor = '#FF0000'
      setTimeout(() => {
         this.styles[this.item].backgroundColor = this.colors[Math.floor(Math.random() * (4 - 0) + 0)]
      }, this.difficulty/2);
    },


    playSound () {
        let aud =  new Audio('http://soundbible.com/mp3/Air Plane Ding-SoundBible.com-496729130.mp3')
        aud.play()
   },


  async buttonClick(){
    this.flag =true
    this.arrId = []
    for(let i = 0; i < this.size;i++){ 
    await new Promise((res)=>{
      setTimeout(()=>{
        let m = Math.floor(Math.random() * (4 - 0) + 0)
        this.clickElement(m)
        this.pushMassive(m)
          res()
      },this.difficulty)
    })
    }
    this.paly = true
   },

  },


  computed:{
    givenLvlv(){
      return  this.lvl
    }
  },
  watch:{
    indx(pref,next){
      console.log(pref,next);
    }
  }

}
</script>

<style src="./style.css">
</style>
